.box1 {
  background-color: rgba(107, 181, 227, 0.53);
  border-radius: 9px;
}

.box2 {
  background-color: rgba(107, 181, 227, 0.53);
  display: table-cell;
  height: 45px;
  width: 55px;
  vertical-align: middle;
  text-align: center;
  border-radius: 9px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: rgb(239, 239, 239);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}